'use client';

//import { cookies } from 'next/headers';
//import { redirect } from 'next/navigation';
import BarChart from "@/components/chart/Chart";
import Graph from "@/components/dashboard/Graph";
import Time from "@/components/dashboard/Time";
import Principal from "@/components/Principal";
// import { useAuth } from './contexts/authProvider';
import ProtectedRoute from '@/components/ProtectedRoute';
import { useAuth } from '@/components/authProvider';


export default async function Home() {
  // const token = (await cookies()).get('token');

  // if (!token) {
    
  //   redirect('/auth/login');
  // }
  const { user } = useAuth();

  return (
    
    <Principal titre="Dashboard">
      {/* <h1 className="p-4 text-xl">Dashboard</h1> */}

      <ProtectedRoute>
      <div className="max-w-4xl mx-auto">
        <h1 className="text-3xl font-bold mb-6">Bienvenue dans votre application</h1>
        
        {user && (
          <div className="bg-white p-6 rounded-lg shadow-md">
            <h2 className="text-xl font-semibold mb-4">Vos informations</h2>
            <div className="space-y-2">
              <p><span className="font-medium">Nom d'utilisateur:</span> {user.username}</p>
              <p><span className="font-medium">Email:</span> {user.email}</p>
              <p><span className="font-medium">ID:</span> {user.id}</p>
            </div>
          </div>
        )}
        
        <div className="mt-8 bg-blue-50 p-6 rounded-lg border border-blue-200">
          <h2 className="text-xl font-semibold mb-2 text-blue-800">Contenu sécurisé</h2>
          <p className="text-blue-700">
            Ce contenu n'est visible que pour les utilisateurs authentifiés.
            Vous êtes actuellement connecté et avez accès à toutes les fonctionnalités de l'application.
          </p>
        </div>
      </div>
    </ProtectedRoute>

      <Graph />
      <Time />
      <BarChart />
    </Principal>
  );
}

